.manual {

    &__list {
        display: flex;
        flex-direction: column;

        list-style: none;
        counter-reset: counter-manual;
    }

    &__item {
        --manualNumber: counter(counter-manual) ".";
        counter-increment: counter-manual;

        &:nth-child(-n + 9) {
            --manualNumber: "0" counter(counter-manual) ".";
        }
    }

    &__card {
        &-num p::before {
            content: var(--manualNumber);
            
        }
    }

}